.nav {
  text-transform: uppercase;
  margin: 0 120px;

  &__list {
    display: flex;
    justify-content: space-between;

    @media (max-width: 950px) {
      position: absolute;
      flex-direction: column;
      justify-content: space-around;

      height: 100%;
      background-color: blue;

      padding-top: 120px;

      top: 0;
      right: 0;

      display: none;
    }
  }

  &__toggler {
    display: none;
  }

  &__toggler:checked ~ &__list {
    display: block;
  }

  &__toggler-label {
    display: none;

    position: absolute;
    top: 42px;
    right: 120px;
    z-index: 2;

    &::after {
      content: "\2630";
      height: 12px;
      width: 18px;
      color: white;
      font-size: 24px;
    }

    @media (max-width: 950px) {
      display: block;
    }
  }

  &__toggler:checked + &__toggler-label::after {
    content: "x";
  }

  &__item {
    padding: 8px 24px;
    border-radius: 8px;
    list-style: none;
    transition: 0.3s;

    &-active {
      background-color: rgba(255, 255, 255, 0.2);
    }

    &:hover,
    :active {
      opacity: 0.8;
    }

    @media (max-width: 950px) {
      margin-bottom: 70px;
    }
  }

  &__link {
    color: #fff;
    text-decoration: none;
    letter-spacing: 2px;
  }
}
