.promo {
  position: relative;
  margin-bottom: 104px;
  padding-bottom: 48px;
  width: 100%;
  max-height: 700px;
  min-height: 320px;
  background-image: url(../images/header.jpg);
  background-size: cover;

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 314px;
    padding: 24px 120px 0;
  }

  &__call {
    width: 18px;
    height: 18px;
    background: url(../images/call.svg);
    text-decoration: none;
    opacity: 0;
    display: none;
  }

  &__action {
    padding-bottom: 8px;
    border-bottom: 1px solid $main-color;
  }

  &__link {
    text-transform: uppercase;
    text-decoration: none;
    line-height: 16px;
    letter-spacing: 2px;
    color: $main-color;
    transition-duration: 0.3s;

    &:hover,
    :visited,
    :active {
      opacity: 0.8;
    }
  }

  &__info {
    position: relative;
    margin-bottom: 85px;
    margin-left: 120px;
    color: $main-color;

    &::before {
      content: "";
      position: absolute;
      display: block;
      left: 0;
      top: 0;

      background-color: $main-color;
      width: 35px;
      height: 2px;
      border-radius: 2px;
    }

    @media (max-width: 950px) {
      margin-left: 10px;
    }
  }

  &__title {
    font-size: 60px;
  }
}

@media (max-width: 950px) {
  .promo__link {
    display: none;
  }

  .promo__call {
    display: block;
    opacity: 100;
  }
}
