.consultation {
  &__item {
    display: grid;
    grid-template-columns: repeat(2, 412px);
    justify-content: space-between;

    @media (max-width: 950px) {
      display: flex;
      flex-direction: column;
    }
  }

  &__description {
    max-width: 412px;
  }

  &__description > :first-child {
    color: #0075ff;
    font-size: 24px;
    line-height: 30px;
  }
}
