.section {
  margin: 0 120px 152px;

  @media (max-width: 950px) {
    margin: 10px;
  }

  &__title {
    display: block;
    margin-bottom: 93px;
    text-align: center;
    font-size: 48px;
    line-height: 48px;
    color: #000;

    &::before {
      content: "";
      width: 23px;
      height: 16px;
      position: relative;
      top: 70px;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      background: url(../images/crown.svg) center no-repeat;
    }
  }
}
