.info {
  display: flex;
  justify-content: space-between;

  &__title {
    color: rgb(0, 0, 0);

    font-size: 48px;
    letter-spacing: -1.5px;

    &-active {
      color: #0075ff;
      font-size: inherit;
      text-decoration: none;
      transition: 0.3s;

      &:hover {
        opacity: 0.5;
      }
    }
  }

  &__description {
    max-width: 357px;
    & > :first-child {
      color: #0075ff;
      font-size: 24px;
      line-height: 30px;
    }
  }
}

p {
  font-size: 16px;
  font-weight: 300;
  margin-bottom: 27px;
  color: #3e3e3e;
}

strong {
  font-size: 16px;
}
