.contact {
  &__item {
    display: grid;
    grid-template-columns: repeat(2, 470px);
    justify-content: space-between;

    @media (max-width: 950px) {
      display: flex;
      flex-direction: column;
    }
  }

  &__info {
    display: flex;
    flex-direction: space-between;
  }

  &__about {
    margin-right: 107px;
  }

  &__link {
    text-decoration: none;
    color: #3e3e3e;
    font-size: 16px;
    font-weight: 300;
    line-height: 27px;
    transition: 0.3s;

    &:hover {
      color: #0075ff;
    }
  }
}
